.ins-tab-layout > .ins-tabs div {
  display: inline-block;
  margin-right: 30px;
  text-align: center;
  cursor: pointer;
  color: var(--pf-global--link--Color); }
  .ins-tab-layout > .ins-tabs div.active {
    border-bottom: 3px var(--pf-global--link--Color) solid; }
  .ins-tab-layout > .ins-tabs div:hover {
    color: var(--pf-global--link--Color--hover);
    border-bottom: 3px var(--pf-global--link--Color--hover) solid; }
